<script>
export default {
    name: 'zgmb',
    inject:['reload'],
    data(){
        return{
            creator_id: null,
            xuexiao: '',
            zhuanye: '',
            kecheng: '',
            QWE00: [],
            QWE01: [],
            QWE02: [],
            ASD00: [],
            ASD01: [],
            ASD02: [],
            ZXC00: [],
            ZXC01: [],
            ZXC02: [],
            badge1:true,
            badge2:true,
            badge3:true,
            badge4:true,
            sannian:"",
            xiangmu:"",
            renwu:"",
            danxiang:"",
            userInfo: {     //用户信息
                name: '',
                dj: '',
                gender: '',
                user_id: '',
                user_status: '',
            },
            userlevel: '1',     //根据userlevel来判断显示的层级
            activeName: 'first',
            windowWidth:null,
            ndgh:[
                [],
                [],
                []
            ],
            gzgh:[
                [],
                [],
                []
            ],
            xmqd:[
                [],
                [],
                []
            ],
            gzrw:[
                [],
                [],
                []
            ],
            ellipsis: true,
        }
    },
    methods:{
        dataDetails(){
            var cur = document.getElementsByClassName("add")
            cur[0].style.cursor = "pointer"
        },
        handleClick(tab, event) {
            console.log(tab, event);
        },
        detail(data){
            console.log(data)
            localStorage.setItem("creid",data.creator_id)
            this.$router.push({
                path:'/detail',
                name:'detail',
                query:{
                    id: data.id,
                    userid: window.sessionStorage.getItem('user_id'),
                }
            })
        },
        addProject(){
            var that = this;
            this.$router.push({
                path:'/add',
                name:'add',
                query:{     //用户信息
                    creator_id: that.creator_id
                }
            })
        },
        cancelFollow(data,event){
            var event=event||window.event;
            console.log(data,event)
            if(event.stopPropagation){
                event.stopPropagation();
            }else{
                event.cancelBubble = true;
            }
            data.follow = 0
            var _this = this
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/mb/targetFollow.action",
                data: {"targetid":data.id,"follow":data.follow},
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    if(res.mess == true){
                        _this.$message.warning('已取消关注！')
                    }
                }
            });
        },
        isFollow(data,event){
            var event=event||window.event;
            // console.log(data, event, 'data&event')
            if(event.stopPropagation){ // 阻止冒泡
                event.stopPropagation();
            }else{
                event.cancelBubble = true;
            }
            data.follow = 1
            var _this = this
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/mb/targetFollow.action",
                data: {"targetid":data.id,"follow":data.follow},
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    if(res.mess == true){
                        _this.$message.success('已关注！')
                    }
                }
            });
        },
        win(a,b){
            if(this.windowWidth >= 1700){
                if(a.length % 5 != 0){
                    if(a.length % 5 == 1){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }else if(a.length % 5 == 2){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }else if(a.length % 5 == 3){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }else if(a.length % 5 == 4){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }
                }
            }else if(this.windowWidth >= 1392 && this.windowWidth < 1700){
                if(a.length % 4 != 0){
                    if(a.length % 4 == 1){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }else if(a.length % 4 == 2){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }else if(a.length % 4 == 3){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }
                }
            }else if(this.windowWidth >= 1100 && this.windowWidth < 1392){
                if(a.length % 3 != 0){
                    if(a.length % 3 == 1){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div><div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }else if(a.length % 3 == 2){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }
                }
            }else{
                if(a.length % 2 != 0){
                    if(a.length % 2 == 1){
                        var col = '<div class="zggzgh el-col el-col-8" :span="8" style="visibility:hidden;"></div>'
                        b.append(col)
                    }
                }
            }
        },
        userdj(){
            this.userlevel = window.sessionStorage.getItem('userdj');
            var btn = document.getElementsByClassName("item102");
            // console.log(btn)
            if(this.userlevel == 1){
                this.activeName = 'first'
            }else if(this.userlevel == 2){
                this.activeName = 'first'
                this.badge1 = false;
                // btn[0].style.marginLeft = "8.7rem";
            }else if(this.userlevel == 3){
                this.activeName = 'first'
                this.badge1 = false;
                this.badge2 = false;
            }
        },
        getFenjie(){
            var that = this;
            var userid = window.sessionStorage.getItem('user_id');
            this.creator_id = userid;
            console.log(userid, 'userid')
            $.ajax({
                type: "post",
                url: that.baseUrl+"/ydjxzg/mb/ndgh.action",
                data: {
                    'userid':userid
                },
                dataType: "json",
                success: function (res) {
                    console.log(res);

                    // that.xuexiao = res.list[0].length;
                    // that.zhuanye = res.list[1].length;
                    // that.kecheng = res.list[2].length;

                    that.xuexiao = res.xuexiao[0].length + res.xuexiao[1].length + res.xuexiao[2].length;
                    that.zhuanye = res.zhuanye[0].length + res.zhuanye[1].length + res.zhuanye[2].length;
                    that.kecheng = res.kecheng[0].length + res.kecheng[1].length + res.kecheng[2].length;

                    // 学校层面数据开始
                    // 我负责的
                    that.QWE00 = res.xuexiao[0];
                    that.QWE01 = res.xuexiao[1];
                    that.QWE02 = res.xuexiao[2];
                    // 学校层面数据结束

                    // 专业层面数据开始
                    that.ASD00 = res.zhuanye[0];
                    that.ASD01 = res.zhuanye[1];
                    that.ASD02 = res.zhuanye[2];
                    // 专业层面数据结束

                    // 课程层面数据开始
                    that.ZXC00 = res.kecheng[0];
                    that.ZXC01 = res.kecheng[1];
                    that.ZXC02 = res.kecheng[2];
                    // 课程层面数据结束

                    that.$children[0].$children[0].$forceUpdate(); // 强制渲染，否则角标数字不显示
                }
            });
        }
    },
    created() {
        if(this.$route.query.user_id){
            this.userInfo = this.$route.query
            console.log(this.userInfo)
            sessionStorage.setItem("userdj",this.userInfo.dj)
            sessionStorage.setItem("usergender",this.userInfo.gender)
            sessionStorage.setItem("user",this.userInfo.name);
            sessionStorage.setItem("user_id",this.userInfo.user_id);
            localStorage.setItem("user_id",this.userInfo.user_id)
            sessionStorage.setItem("login","isLogin");
            var userid = this.userInfo.user_id
            var dj = this.userInfo.dj
        }else if(sessionStorage.getItem('user_id')){
            // var userid = sessionStorage.getItem('user_id')
            // var dj = sessionStorage.getItem('userdj')
        }else if(!this.$route.query.user_id && !window.sessionStorage.getItem('user_id')){
            //window.location.href = 'http://zg.demo.xqit.net/#/login'    //开发
            window.location.href = 'http://zg.demo.xqit.net/ydjxzg/index.html#/login'    //线上
        }
        this.userdj()
        var _this = this;
        this.getFenjie();
    },
    mounted() {
        // console.log(this.$route.query)
        localStorage.setItem("userId",this.$route.query.user_id);
        // console.log(localStorage.getItem('userId'))
        // console.log(this.$route.query.user_id)
        localStorage.setItem("dj",this.$route.query.dj)
        localStorage.setItem("xm",this.$route.query.name)
        var _this = this
        setTimeout(function () {
            _this.windowWidth = document.documentElement.offsetWidth;
            _this.win($(".zggzgh"),$('#zggh'))
            _this.win($(".zggzgh_p"),$('#zggh_p'))
            _this.win($(".zggzgh_h"),$('#zggh_h'))
            _this.win($(".zxgzgh"),$('#zxgh'))
            _this.win($(".zxgzgh_p"),$('#zxgh_p'))
            _this.win($(".zxgzgh_h"),$('#zxgh_h'))
            _this.win($(".xmgh"),$('#xmqd'))
            _this.win($(".xmgh_p"),$('#xmqd_p'))
            _this.win($(".xmgh_h"),$('#xmqd_h'))
            _this.win($(".gzgh"),$('#gzrw'))
            _this.win($(".gzgh_p"),$('#gzrw_p'))
            _this.win($(".gzgh_h"),$('#gzrw_h'))
        },100)

        // var aa = document.getElementById('sanNianGuiHua');
        // aa.click();
    },
    watch: {
        '$route':{
            handler(route){
                var _this = this
                if(route.query.shuaxin == 1){
                    //_this.$router.go(0)
                    _this.reload()    //每次进入/zgmb都将刷新界面,因此注释
                }
            },
            deep: true
        }
    },
}
</script>

